import React, { Component } from "react"
import { Link } from "gatsby"
import styled from "styled-components"

import "../styles/global.css"

const StyledUl = styled.ul`
    list-style: none;
    grid-column: 2/8;
    align-self: flex-end;
    margin: 0 0 80px 0;
    line-height: 40px;

    @media(min-width: 992px) {
        margin: 20px 0;
    }
`

const StyledLink = styled(Link)`
    text-decoration: none;
    font-family: 'Druk-Wide-Medium';
    color: #0A5640;
    font-size: 2em;

    @media (min-width: 992px) {
        text-decoration: none;
        font-family: 'Druk-Wide-Medium';
        color: #0A5640;
        font-size: 44px;
    }
`;

class Menu extends React.Component {
    constructor() {
        super();
        
    }

    onClick() {
        console.log(document.querySelector('.menutop'));
        document.querySelector('.menutop').style.transform = 'translateY(0%)';
    }

    render() {
        return(
            <StyledUl>
            <li><StyledLink onClick={this.onClick} to='/workspaces'>Workspaces</StyledLink></li>
            <li><StyledLink onClick={this.onClick} to="/about" >About</StyledLink></li>
            <li><StyledLink onClick={this.onClick} to="/stpauli" >St. Pauli</StyledLink></li>
            <li><StyledLink onClick={this.onClick} to="/kontakt" >Kontakt</StyledLink></li>
            </StyledUl>  
        )
    }
}


export default Menu