    class IndexPage extends React.PureComponent {

      componentDidMount(){
        document.querySelector('.menutop').style.transform = 'translateY(-100%)';

      }

      render() {
        const { children, location } = this.props;
        

        console.log(location);
    
        // To enable page transitions on mount / initial load,
        // use the prop `animateOnMount={true}` on `PoseGroup`.
        return (
          <>
          <Transition style={{overflow: 'hidden', height:'100%'}}>
          <SEO title="Home" keywords={[`gemeindehaus`, `workspace`, `coworking`]} />
          <Container>
            <ImgTitle src={title} />
            <Menu location={this.props.location} />
            <OtherLinks />
          </Container>
          <BackgroundVideo />
          <BackgroundVideo />
          </Transition>
            </>
        )
      }
    }

export default IndexPage

export const pageQuery = graphql`
  query {
    imageOne: file(relativePath: { eq: "prices.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    imageTwo: file(relativePath: { eq: "info.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`